.hero {
  margin-right: 200px;
  align-self: center;
  color: #333;
}

.emotion-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.emotion-wheel {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 500px;
  height: 500px;
}

.emote {
  position: relative;
  width: 40px;
  height: 40px;
  margin-right: 100px;
  border-radius: 40px;
  -webkit-text-stroke: 1px black;
  background: #aaa;
  color: white;
  letter-spacing: 1px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  box-shadow: inset 0px 5px 7px -3px rgba(255, 255, 255, 0.5);
}

.emote:hover {
  box-shadow: inset 0px 5px 7px -3px rgba(255, 255, 255, 0.5);
}

/* .emote:active {
    box-shadow: inset 0px 5px 7px -3px rgba(0, 0, 0, 0.3);
  } */

.emotion {
  position: absolute;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  -webkit-text-stroke: 1px black;
  box-shadow: inset 0px 5px 7px -3px rgba(255, 255, 255, 0.5);
}

.emotion:hover {
  box-shadow: inset 0px 5px 7px -3px rgba(255, 255, 255, 0.5);
}

.emotion:active {
  box-shadow: inset 0px 5px 7px -3px rgba(0, 0, 0, 0.3);
}

#select-layer {
  font-family: "Open Sans", sans-serif;
  font-size: 0.9em;
  font-weight: 500;
  border-color: grey;
  border-radius: 50px;
  padding: 0.3em 1em;
  margin-right: 100px;
}
